import {TABS_CHANGED} from '../consts';

const request = (options,success,error) =>{
    $.ajax(options)
        .done((data)=>{
            if(data.success){
                success(data)
            }else{
                error(data)
            }
        })
        .fail((err) => {
            error(err);
        })
};
const requestMenuList = () => {

}

export function tabsChangedFn(text){
    console.log(text);
    return {
        type:TABS_CHANGED,
        text
    }
}


